import Link from 'next/link';
import { Mutation } from 'react-apollo';
import NavStyles from './styles/NavStyles';
import User from './User';
import Signout from './Signout';
import { TOGGLE_CART_MUTATION, CLOSE_CART_MUTATION } from '../components/Cart';
import CartCount from './CartCount';

const Nav = () => (
  <User>
    {/* ^^exposes the currently logged in user and pass down the CURRENT USER QUERY prop */}
    {({ data: { me } }) => (
      <NavStyles data-test='nav'>
        <Mutation mutation={CLOSE_CART_MUTATION}>
          {closeCart => <button onClick={closeCart}>close</button>}
        </Mutation>
        <Link href='/items'>
          <a>Shop</a>
        </Link>
        {me && (
          <>
            <Link href='/sell'>
              <a>Sell</a>
            </Link>
            <Link href='/orders'>
              <a>Orders</a>
            </Link>
            <Link href='/me'>
              <a>Account</a>
            </Link>
            <Signout />
            <Mutation mutation={TOGGLE_CART_MUTATION}>
              {toggleCart => (
                <button onClick={toggleCart}>
                  My Cart
                  <CartCount
                    count={me.cart.reduce(
                      (tally, cartItem) => tally + cartItem.quantity,
                      0
                    )}></CartCount>
                </button>
              )}
            </Mutation>
          </>
        )}
        {!me && (
          <Link href='/signup'>
            <a>Sign In</a>
          </Link>
        )}
      </NavStyles>
    )}
  </User>
);

export default Nav;
